@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: Inter,system-ui,-apple-system;
}

#hero{
    /* border: 1px solid red; */
    position: absolute;
    height: 100vh;
    width: 100%;

    display: flex;
    flex-direction: column;
}

#header{
    /* border: 1px solid green; */
    height: 10%;
    padding: 2vh;
}

#article{
    /* border: 1px solid green; */
    height: 25%;

    display: flex;
    flex-direction: column;
}

#inputContainer{
    display: flex;
    justify-content: space-between;
}

/* input fields */
.text{
    padding: 1vw;
    margin: 1vw;
    width: 40%;
    border: 1px solid #cfcfcf;
    border-radius: 4px;
}

#btn{
    padding: 1vw;
    margin: 1vw;
    border: 1px solid #cfcfcf;
    border-radius: 4px;

    font-size: 1rem;
    cursor: pointer;
}

#btn:hover{
    background-color: #efefef92;
}

#section{
    /* border: 1px solid forestgreen; */
    height: 65%;

    display: flex;
    justify-content: center;
    align-items: center;

    overflow: auto;
    position: relative;
}

#img{
    height: 90%;
    width: 50%;
    position: relative;
}


.memeText{
    position:absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 1em 0;
    padding: 0 5em;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}


#topText{
    top: 0;
}

#bottomText{
    bottom: 0;
}